import ShowJob from "./components/ShowJob.vue";
import AddJob from "./components/AddJob.vue";
import SingJob from "./components/SingJob";
import ShowMyslef from "./components/ShowMyslef";

export default [
  {path: "/", component: ShowJob},
  {path: "/add", component: AddJob},
  {path: "/job/:id",  component: SingJob},
  {path: "/show-myself", component: ShowMyslef},
]



