<template>
  <div id="show-job">
    <h1>项目经验总览</h1>
    <input type="text" v-model="search" placeholder="搜索">
    <div v-for="jobArray in filteredBlogs" class="single-job">
      <router-link v-bind:to="'/job/' + jobArray.id">
        <h2 v-rainbow>{{jobArray.title}}</h2>
        <h3>{{jobArray.content}}</h3>
        <article>
          {{jobArray.body | snippet}}
        </article>
      </router-link>
    </div>
  </div>
</template>

<script>
    export default {
      name: "ShowJob",
      data(){
        return{
          jobArray: [],
          search: "",
        }
      },
      computed: {
        filteredBlogs(){
          return this.jobArray.filter((job)=>{
            return job.title.match(this.search);
          })
        },
      },
      filters:{
        "snippet": function (value) {
          if (value) {
            return value.slice(0, 150) + " ...";
          }

        }
        // snippet(value){
        //   return value.slice(0, 150) + " ...";
        // }
      },
      directives: {
        "rainbow": {
          bind(el, binding, vnode) {
            el.style.color = "#" + Math.random().toString(16).slice(2, 8);
          }
        }
      },
      created() {
        this.$http.get("./../static/info.json").then((res)=>{
        // this.$http.get("http://jsonplaceholder.typicode.com/posts").then((res)=>{
          this.jobArray = res.data;
        });
      }
    }
</script>

<style scoped>
#show-job{
  max-width: 600px;
  margin: 0 auto;
}
  .single-job{
    padding: 20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eee;
    border: 1px dotted #aaa;
  }
  #show-job a{
    color: #444;
    text-decoration: none;
  }
  input[type="text"]{
    padding: 8px;
    width: 100%;
    box-sizing: border-box;
  }
</style>



