<template>
    <div id="sing-job">
      <h1 v-rainbow>{{jobInfo.title}}</h1>
      <hr>
      <span>
        <p>项目分类：{{jobInfo.proClass}}</p>
        <p>开发模式：{{jobInfo.proNum}}</p>
        <p>技术语言：</p>
        <ul>
          <li v-for="job in jobInfo.proLanguage">
            {{job}}
          </li>
        </ul>
        <hr>
        <h2>项目描述：</h2>
        <p>{{jobInfo.content}}</p>
      </span>

    </div>
</template>

<script>
    export default {
      name: "SingJob",
      data(){
        return{
          id: this.$route.params.id,
          jobInfo: {}
        }
      },
      directives: {
        "rainbow": {
          bind(el, binding, vnode) {
            el.style.color = "#" + Math.random().toString(16).slice(2, 8);
          }
        }
      },
      created() {
        // this.$http.get("./../static/info.json").then((res)=>{
        this.$http.get("./../static/info" + this.id + ".json").then((data)=>{
        // this.$http.get("http://jsonplaceholder.typicode.com/posts/" + this.id).then((data)=>{
          console.log(data);
          this.jobInfo = data.data;
        });
      }
    }
</script>

<style scoped>
  #sing-job{
    max-width: 960px;
    margin: 0 auto;
    padding: 20px;
    background: #eee;
    border: 1px dotted #aaa;
  }
</style>



