<template>
    <div id="add-job">
      <h1>添加项目经验</h1>
      <form v-if="!submmited">
        <label>项目概述</label>
        <input type="text" v-model="job.title" required />

        <label>项目描述</label>
        <textarea v-model="job.content"></textarea>

        <label>项目类型</label>
        <select v-model="job.proClass">
          <option v-for="proClassAll in proClassAll">
            {{proClassAll }}
          </option>
        </select>
        <label>开发模式</label>
        <select v-model="job.proNum">
          <option v-for="proNum in proNum">
            {{proNum}}
          </option>
        </select>
        <label>技术语言</label>
        <div id="checkboxes">
          <label>c++</label>
          <input type="checkbox" value="c++" v-model="job.proLanguage">
          <label>js</label>
          <input type="checkbox" value="js" v-model="job.proLanguage">
          <label>ts</label>
          <input type="checkbox" value="ts" v-model="job.proLanguage">
          <label>Andorid</label>
          <input type="checkbox" value="Andorid" v-model="job.proLanguage">
          <label>oc</label>
          <input type="checkbox" value="oc" v-model="job.proLanguage">
          <label>php</label>
          <input type="checkbox" value="php" v-model="job.proLanguage">
          <label>html</label>
          <input type="checkbox" value="html" v-model="job.proLanguage">
          <label>css</label>
          <input type="checkbox" value="css" v-model="job.proLanguage">
        </div>
        <button v-on:click.prevent="onClickAddPro">添加项目经验</button>
      </form>

      <div v-if="submmited">
        <h3>发布成功</h3>
      </div>

      <hr>

      <div id="preview">
        <h2>项目总览</h2>
        <p>项目标题：{{job.title}}</p>
        <p>项目描述：</p>
        <p>{{job.content}}</p>
        <p>项目分类：{{job.proClass}}</p>
        <p>开发模式：{{job.proNum}}</p>
        <p>技术语言：</p>
        <ul>
          <li v-for="proLanguage in job.proLanguage">
            {{proLanguage}}
          </li>
        </ul>
      </div>
    </div>
</template>

<script>
    export default {
      name: "AddJob",
      data(){
        return {
          job:{
            title: "",
            content: "",
            proClass: "",
            proLanguage: [],
            proNum: "",
          },
          proNum: ["团队开发", "独立开发"],
          proClassAll: ["Vue", "Cocos2dx", "CocosCreator", "小游戏", "小程序"],
          submmited: false,
        }
      },
      methods: {
        onClickAddPro(){
          console.log(this.job);
          console.log(JSON.stringify(this.job));
          // this.$http.post("http://jsonplaceholder.typicode.com/posts", {
          //   titlle: this.job.title,
          //   body: this.job.content,
          //   userId: 1
          // }).then((data)=>{
          //   console.log(data);
          //   this.submmited = true;
          // })
        },
      }
    }
</script>

<style scoped>
#add-job *{
  box-sizing: border-box;
}
#add-job {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}
  label{
    display: block;
    margin: 20px 0 10px;
  }
  input[type="text"],textarea,select{
    display: block;
    width: 100%;
    padding: 8px;
  }
  textarea{
    height: 200px;
  }
  #checkboxes label{
    display: inline-block;
    margin-top: 0;
  }
  #checkboxes input{
    display: inline-block;
    margin-right: 10px;
  }
  button{
    display: block;
    margin: 20px 0;
    background: crimson;
    color: #fff;
    padding: 10px;
    border-radius: 7px;
    font-size: 18px;
    cursor: pointer;
  }

  #preview{
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin: 30px 0;
  }
  h2{
    margin-top: 10px;
  }
</style>



